@import "../../../../design-system/src/styles.scss";

.shell-footer {
  width: 100%;
  background: $ds-white;
  border-top: 2px solid $ds-primary;
  padding: $ds-spacing-lg;
  display: flex;
  justify-content: end;
  align-items: center;
  font-family: $ds-font-family;
  font-size: $ds-font-size-sm;
  color: $ds-dark;
  font-weight: $ds-font-weight-normal;
  letter-spacing: 0.05em;
  box-shadow: 0 -2px 8px rgba($ds-primary, 0.05);
}

@media (max-width: 700px) {
  .shell-footer {
    padding: $ds-spacing-md 0;
    font-size: $ds-font-size-xs;
  }
}
